<template>
  <div class="h-full w-full border-2 border-grey-01 py-3 rounded-md text-center flex items-center justify-center">
    <Dropdown :hide-on-click="true" trigger="click" placement="bottom" size="medium">
      <div class="flex items-center justify-center">
        <span class="material-icons text-base lg:mr-2">switch_account</span>
        <span class="text-base lg:inline-block hidden">{{ companyNow }}</span>
      </div>
      <template #dropdown>
        <DropdownMenu>
          <DropdownItem v-for="item in companies" :key="item.id" @click="switchCompany(item)" :disabled="item.company_name === companyNow" class="hover:bg-grey-lighten">
            <div>
              <span class="material-icons text-lg text-teal-04" :class="{ 'opacity-0': item.company_name !== companyNow }">check_circle</span>
              <span class="mx-5 text-sm">{{ item.company_name }}</span>
            </div>
          </DropdownItem>
        </DropdownMenu>
      </template>
    </Dropdown>
  </div>
</template>

<script>
import { defineComponent, computed } from 'vue'
import { useStore } from 'vuex'

export default defineComponent({
  setup() {
    const store = useStore()

    const switchCompany = async data => {
      await store.dispatch('user/switchCompany', data)
      location.reload()
    }
    const companies = computed(() => {
      return store.state.user.me.companies || []
    })
    const companyNow = computed(() => {
      return store.state.user.me.company_name || []
    })

    return {
      companies,
      companyNow,
      switchCompany
    }
  },
});
</script>
