<template>
	<div class="subscription-plan-feature">
		<ul v-for="featureGroup in currentFeatures">
			<li v-for="feature in featureGroup" class="text-teal-04 flex flex-start gap-4 my-1">
				<i class="material-icons">check_circle</i>
				<span class="block mt-1">{{ feature }}</span>
			</li>
		</ul>
	</div>
</template>
<script>
import { defineComponent, computed } from 'vue'
export default defineComponent({
	name: 'SubscriptionPlanFeature',
	props: {
		features: {
			type: Array,
			default: () => ([])
		}
	},
	setup(props) {
		const currentFeatures = computed(() => {
			const featureGroup = []
			if (props.features.length > 9) {
				const features = [...props.features]
				const splitedFeatures = features.splice(Math.floor(features.length / 2), features.length)
				featureGroup.push(features)
				featureGroup.push(splitedFeatures)
			} else {
				featureGroup.push(props.features)
			}
			return featureGroup
		})
		return {
			currentFeatures
		}
	}
})
</script>
<style lang="scss">
.subscription-plan-feature {
	@apply grid lg:grid-cols-2 grid-cols-1;
}
</style>