<script setup>
import { reactive, watch } from 'vue'
import { useRouter } from 'vue-router'
import { tryCatchRequest } from 'utils/request'
import { useMultipleCompany } from 'store/pinia/layouts/multiple-company'

import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'

const store = useMultipleCompany()
const router = useRouter()
const data = reactive({})

const onEdit = () => {
  store.companyDetail.modal = false
  store.unlink.modal = false
  router.push({
    name: 'settingCompanyProfile'
  })
}

const onUnlink = () => {
  store.unlink.modal = true
}

watch(() => store.companyDetail.modal, async (val) => {
  if (val) {
    await tryCatchRequest(
      store.companyDetail,
      () => store.$getCompany()
    )
    Object.assign(data, store.companyDetail.result)
  }
})
</script>

<template>
  <Modal
    v-model="store.companyDetail.modal"
    size="xs"
    title="Company Details"
    :no-close="store.companyDetail.isLoading">
    <template #header>
      <div class="flex items-center justify-between">
        <h1 class="text-2xl font-medium">Company Details</h1>
        <Button
          v-if="data.is_selected && !store.companyDetail.isLoading"
          view="outlined"
          color="teal"
          class="w-24"
          dense
          @click="onEdit">
          <span class="material-icons">edit</span> Edit
        </Button>
        <div
          v-else-if="store.companyDetail.isLoading"
          class="opacity-50">
          <Button
            v-loading="true"
            view="outlined"
            color="teal"
            class="w-24"
            dense>
            <span class="material-icons">edit</span> Edit
          </Button>
        </div>
      </div>
    </template>
    <div  v-loading="store.companyDetail.isLoading">
      <div class="flex gap-4 items-center">
        <img
          v-if="data.company_logo"
          :src="data.company_logo"
          :alt="data.name"
          width="40"
          height="40">
        <div>
          <h1 class="text-xl capitalize font-medium">{{ data.name }}</h1>
          <p class="text-grey-02">{{ data.package_name || '-' }}</p>
        </div>
      </div>
      <div class="mt-8 grid grid-cols-2 gap-6">
        <div class="space-y-1">
          <p class="text-base font-medium">Legal Business Name</p>
          <p class="text-grey-02">{{ data.legal_business_name || '-' }}</p>
        </div>
        <div class="space-y-1">
          <p class="text-base font-medium">Status</p>
          <p class="text-grey-02">{{ data.package_status || '-' }}</p>
        </div>
        <div class="space-y-1">
          <p class="text-base font-medium">Company Email</p>
          <p class="text-grey-02 truncate hover:text-clip" title="{{ data.company_email || '-' }}">{{ data.company_email || '-' }}</p>
        </div>
        <div class="space-y-1">
          <p class="text-base font-medium">Total User</p>
          <p class="text-grey-02">{{ data.total_user || '0' }}</p>
        </div>
        <div class="space-y-1">
          <p class="text-base font-medium">Company Address</p>
          <p class="text-grey-02">{{ data.address || '-' }}</p>
        </div>
        <div class="space-y-1">
          <p class="text-base font-medium">Joined Since</p>
          <p class="text-grey-02">{{ data.created_at || '-' }}</p>
        </div>
      </div>
    </div>
    <template #footer>
      <Tooltip
        placement="top"
        :disabled="data.unlinkable || store.companyDetail.isLoading">
        <template #content>
          <p>Only inactive and invited accounts can be unlinked.</p>
        </template>
        <template #default>
          <div class="inline-block w-full cursor-not-allowed">
            <Button
              color="red"
              class="w-full"
              :disabled="!data.unlinkable || store.companyDetail.isLoading"
              :loading="store.companyDetail.isLoading"
              @click="onUnlink">
              <span class="material-icons">logout</span> Unlink Account
            </Button>
          </div>
        </template>
      </Tooltip>
    </template>
  </Modal>
</template>
