<template>
<template v-if="isMobile">
    <Dropdown trigger="click" :hide-on-click="false" class="w-full">
      <Button :color="isMobile ? 'white' : 'teal'" class="text-base" :class="{'w-full mt-4': isMobile}">Trade-In</Button>
      <template #dropdown>
        <DropdownMenu v-loading="loadingLocation.all" class="p-2 w-96 rounded-t-lg">
            <div class="sticky top-0 py-4 bg-white z-10">
                <label>Choose your trade-in location</label>
                <Input v-model="queryLocation.search" type="text" placeholder="Search..." class="my-4 w-full" icon>
                    <template #icon>
                        <span class="material-icons">search</span>
                    </template>
                </Input>
            </div>
            <router-link v-if="stateLocation?.data?.length" v-for="(location, index) in stateLocation.data" 
            :key="index" 
            :to="{name: location.is_default_mail_in ? 'buybackOnlineCreate' : 'buybackLocalCreate'}"
            >
            <DropdownItem class="rounded-lg">
                <div class="text-base">
                    {{ location.name }} 
                </div>
            </DropdownItem>
            </router-link>
            <div v-else class="px-4 pt-4 pb-2 text-center text-grey-03">
                No location found
            </div>
            <div v-if="!isHidePagination" class="p-4 sticky bottom-0 bg-white">
            <Pagination 
                v-model:page="queryLocation.page" 
                v-model:limit="queryLocation.limit" 
                :data="stateLocation" 
                hideShowEntries 
                hideDetailPage
                small
            />
            </div>
        </DropdownMenu>
      </template>
    </Dropdown>
</template>
<template v-else>
    <div
      v-outside="handleClickOutside"
      class="relative"
    >
      <Button 
        :color="isMobile ? 'white' : 'teal'" 
        class="text-base" 
        :class="{'w-full mt-4': isMobile}"
        @click="toggleVisible"
      >
        Trade-In
      </Button>
      
      <div
        class="bg-white absolute z-50 rounded-lg border-grey-01 shadow-2xl w-96"
        style="left: 0; top: 100%; margin-top: 0.5rem; z-index: 1001"
        v-show="isVisible"
      >
        <div class="p-4 sticky top-0 bg-white z-10 rounded-t-lg">
          <label>Choose your trade-in location</label>
          <Input
            v-model="queryLocation.search"
            placeholder="Search Location"
            class="w-full"
            icon
          >
            <template #icon>
              <span class="material-icons">search</span>
            </template>
          </Input>
        </div>
        
        <div v-loading="loadingLocation.all" class="max-h-96 overflow-y-auto px-4 pb-4">
          <ul v-if="stateLocation.data && stateLocation.data.length > 0">
            <li
              v-for="(item, key) in stateLocation.data"
              :key="key"
              class="text-left p-3 hover:bg-grey-lighten cursor-pointer flex items-center"
              @click="handleClick(item)"
            >
            {{ item.name }}
            </li>
          </ul>
          <div v-else class="px-4 py-6 text-center text-grey-03">
            No location found
          </div>
        </div>
        
        <div v-if="!isHidePagination" class="px-4 pb-4 sticky bottom-0 bg-white z-10 rounded-b-lg">
          <Pagination
            v-model:page="queryLocation.page"
            v-model:limit="queryLocation.limit"
            :data="stateLocation"
            hide-show-entries
            hideDetailPage
            small
            :disabled="loadingLocation.all"
          />
        </div>
      </div>
    </div>
  </template>
</template>

  
  <script>
  import { defineComponent, computed, watch, reactive, onMounted } from 'vue'
  import { useStore } from 'vuex'
  import { useLocations } from 'composable/dashboard'
  import debounce from 'utils/debounce'
  import { useRouter } from 'vue-router'
  
  import Button from 'bbUi/components/button/button.vue'
  import Input from 'bbUi/components/input/input.vue'
  import Pagination from 'components/Pagination.vue'
  
  export default defineComponent({
    name: 'TradeInButton',
    components: {
      Button,
      Input,
      Pagination
    },
    props: {
      isMobile: {
        type: Boolean,
        default: false
      }
    },
    setup() {
      const { locations, stateFiltered: stateLocation, query: queryLocation, loading: loadingLocation } = useLocations(true)
      const store = useStore()
      const router = useRouter()
      const state = reactive({
        isVisible: false
      })
  
      const isHidePagination = computed(() =>
        stateLocation.value.data ? stateLocation.value.total <= queryLocation.limit : false
      )
  
      const toggleVisible = () => {
        state.isVisible = !state.isVisible
      }
  
      const handleClickOutside = () => {
        state.isVisible = false
        queryLocation.page = 1
        queryLocation.search = ''
      }

      const handleClick = async (item) => {
        try {
          loadingLocation.all = true;
          
          const targetRoute = item.is_default_mail_in ? 'buybackOnlineCreate' : 'buybackLocalCreate';
          const routeObj = router.resolve({ name: targetRoute });
          const targetPath = routeObj.href;
          
          await store.dispatch('dashboard/changeLocation', {
            location_id: item.id
          });
          
          state.isVisible = false;
          queryLocation.page = 1;
          queryLocation.search = '';
          
          window.location.href = targetPath;
        } catch (error) {
          console.error('Navigation error:', error);
          loadingLocation.all = false;
        }
      }
      watch(
        () => ({ search: queryLocation.search }),
        () => {
          queryLocation.page = 1
        }
      )
  
      watch(
        () => ({
          search: queryLocation.search,
          page: queryLocation.page
        }),
        debounce(async () => {
          await locations(() => {}, store.state.user.me.id)
        }, 500),
        {
          deep: true
        }
      )
  
      return {
        stateLocation,
        queryLocation,
        isHidePagination,
        loadingLocation,
        isVisible: computed(() => state.isVisible),
        toggleVisible,
        handleClickOutside,
        handleClick
      }
    }
  })
  </script>