<template>
  <div class="mobile-sidebar">
    <div class="mt-6 flex justify-center items-center">
      <Suspense>
        <MultipleCompany mobile />
      </Suspense>
    </div>
    <div class="buyback px-6 w-full">
      <TradeInButton :isMobile="true" />
    </div>
    <div class="sidebar__menu--new">
      <ul v-for="(menus, n) in menuItems" :key="n">
        <MenuItem v-for="menu in menus" :route="menu" :key="menu.name" @menu:click="closeSidebar" />
      </ul>
    </div>
  </div>
</template>

<script>
import { defineComponent, computed, ref } from 'vue'
import { useStore } from 'vuex'
// import { menu, menuSA } from 'constants/menu'
import { menuSA } from 'constants/menu'
import { useUser } from 'composable/user'
import MultipleCompany from 'components/Features/Layouts/MultipleCompany/MultipleCompany.vue'
import MenuItem from 'components/MenuItem.vue'
import TradeInButton from 'components/TradeInButton.vue'

export default defineComponent({
  components:{
    MultipleCompany,
    MenuItem,
    TradeInButton
  },
  props: {
    isMailin: {
      type: Boolean,
      default: false
    }
  },
  emits: ['closed'],
  setup(props, { emit }) {
    const { me, menu, action: showUser } = useUser()
    const refMobileSidebar = ref(null)
    const store = useStore()
    const options = import.meta.env.MODE === 'super' ? menuSA : menu.value
    const menuItems = computed(() => {
      const exception = ['layoutHelpTOS', 'layoutIntegration', 'layoutSetting']
      const splitMenu = [[], []]
      menu.value.map((item, key) => {
        if (exception.includes(item.name)) {
          splitMenu[1].push(item)
        } else splitMenu[0].push(item)
      })
      return splitMenu
    })
    const routes = computed(() => {
      return store.state.routes
    })
    const handleGetUser = async () => await showUser()
    const closeSidebar = () => {
      emit('closed', true)
    }

    return {
      me,
      routes,
      options,
      menuItems,
      refMobileSidebar,
      handleGetUser,
      closeSidebar
    }
  }
})
</script>