<script setup>
import { reactive, ref, watch } from 'vue'
import { tryCatchRequest } from 'utils/request'
import { useMultipleCompany } from 'store/pinia/layouts/multiple-company'

import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import { ElMessage } from 'element-plus'

const store = useMultipleCompany()
const data = reactive({})
const isConfirm = ref(false)

const onConfirm = async () => {
  await tryCatchRequest(
    store.unlink,
    () => store.$unlinkAccount()
  ).then(() => {
    ElMessage({
      type: 'success',
      message: 'Unlink account successfully'
    })

    store.company.result = store.generateList(
      store.company.result.filter(company =>
        company.company_token !== store.companyDetail.token
      )
    )
    store.companyDetail.modal = false
    store.unlink.modal = false
    isConfirm.value = false
  })
}
const onCancel = () => {
  store.unlink.modal = false
  isConfirm.value = false
}

watch(() => store.unlink.modal, (val) => {
  if (val) {
    Object.assign(data, store.companyDetail.result)
  }
})
</script>

<template>
  <Modal
    v-model="store.unlink.modal"
    size="xs"
    hide-header
    hide-footer>
    <template #default>
      <div class="py-8">
        <img class="mx-auto" src="@/assets/icon/attention.svg" alt="attention">
        <h1 class="text-2xl font-medium mt-6 text-center">Unlink Account?</h1>
        <p class="text-center text-grey-03 mt-6">
          {{
            data.tag === 'admin'
              ? 'Unlinking will immediately remove your access to this account. You will only be able to regain access if the account is active and the admin sends you a new invitation, or if you have direct login credentials for it.'
              : 'Unlinking will immediately remove your access to this account'
           }}
        </p>
        <Checkbox
          v-model="isConfirm"
          class="mt-6">
          <p class="font-normal mt-4">
            I have confirmed that I wish to proceed, as this cannot be reversed once completed.
          </p>
        </Checkbox>
        <div class="flex justify-center items-center gap-6 mt-10">
          <Button
            color="red"
            :disabled="!isConfirm"
            :loading="store.unlink.isLoading"
            @click="onConfirm">
            Yes, Leave
          </Button>
          <Button
            :disabled="store.unlink.isLoading"
            view="smooth"
            color="teal"
            @click="onCancel">
            Cancel
          </Button>
        </div>
      </div>
    </template>
  </Modal>
</template>
