<template>
  <div class="grid grid-cols-1">
    <div class="mb-5">
      <Input v-model="data.current_password" :type="showPassword.current_password ? 'text' : 'password'" label="Current Password" :view="validation.current_password.$invalid && 'danger' || 'default'" block icon right>
        <template #icon>
          <span class="material-icons block cursor-pointer text-grey-03" @click="showPassword.current_password = !showPassword.current_password">{{showPassword.current_password ? 'visibility_off' : 'visibility'}}</span>
        </template>
      </Input>
      <span class="text-red-base mt-2 block" v-for="error of validation.current_password.$errors">{{ error.$message }}. </span>
    </div>
    <div class="mb-5">
      <Input v-model="data.new_password" :type="showPassword.new_password ? 'text' : 'password'" label="New Password" :view="validation.new_password.$invalid && 'danger' || 'default'" block icon right autocomplete="new-password">
        <template #icon>
          <span class="material-icons block cursor-pointer text-grey-03" @click="showPassword.new_password = !showPassword.new_password">{{showPassword.new_password ? 'visibility_off' : 'visibility'}}</span>
        </template>
      </Input>
      <span class="text-red-base mt-2 block" v-for="error of validation.new_password.$errors">{{ error.$message }}. </span>
    </div>
    <div class="mb-5">
      <Input v-model="data.confirm_password" :type="showPassword.confirm_password ? 'text' : 'password'" label="Confirm Password" :view="validation.confirm_password.$invalid && 'danger' || 'default'" block icon right autocomplete="new-password">
        <template #icon>
          <span class="material-icons block cursor-pointer text-grey-03" @click="showPassword.confirm_password = !showPassword.confirm_password">{{showPassword.confirm_password ? 'visibility_off' : 'visibility'}}</span>
        </template>
      </Input>
      <span class="text-red-base mt-2 block" v-for="error of validation.confirm_password.$errors">{{ error.$message }}. </span>
      <span class="text-red-base mt-2 block" v-if="error.show">{{ error.message }}</span>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive, watch } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'
import { helpers } from "@vuelidate/validators"
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  components: {
    Input
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        current_password: null,
        new_password: null,
        confirm_password: null
      })
    }
  },
  setup(props) {
    const data = reactive(props.data)
    const showPassword = reactive({
      current_password: null,
      new_password: null,
      confirm_password: null
    })
    const error = reactive({
      show: false,
      message: null
    })
    const rulesChangePassword = {
      current_password: { required },
      // new_password: { required, passwordValidation: helpers.withMessage('At least 6 alphanumerics and include capital letters', helpers.regex(/^(?=.*\d)(?=.*[A-Z])[a-zA-Z0-9]{6,}$/)) },
      new_password: { required, passwordValidation: helpers.withMessage('At least 6 characters and include capital letters', helpers.regex(/^(?=.*?[A-Z]).{6,}$/)) },
      confirm_password: { required }
    }
    const validation = useVuelidate(rulesChangePassword, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid && error.show === false
    }
    watch(data, () => {
      if((data.confirm_password !== data.new_password) && data.confirm_password) error.show = true, error.message = 'Your confirmation password does not match'
      else error.show = false, error.message = null
    })

    return {
      data,
      validation,
      showPassword,
      error,
      validate
    }
  }
})
</script>