<template>
	<div>
		<div v-if="loading" class="flex items-center justify-center h-96">
			<BaseLoading />
		</div>
		<div v-else>
			<div class="mb-6">
				<label class="font-semibold block">Compare Plans</label>
				<RadioGroup v-model="selectedSubscriptionOption">
					<RadioButton v-for="subscriptionOption in subscriptionOptions" :key="subscriptionOption.id"
						:label="subscriptionOption.id">
						{{ subscriptionOption.label }}
					</RadioButton>
				</RadioGroup>
			</div>
			<div class="subscription-plan">
				<div v-for="plan in currentPlans" :key="plan.id">
					<SubscriptionPlanCard @select="$emit('selectPlan', plan)" :id="plan.id" :name="plan.name"
						:description="plan.description" :price="plan.price" :features="plan.features"
						:selected="plan.is_current_plan" :loading="loadingButton" :saving-amount="plan.saving_amount"
						:feature-term="plan.feature_term" :subscription-option="selectedSubscriptionOption" />
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import { defineComponent, computed } from 'vue'
import { usePlan } from 'composable/billing';

import SubscriptionPlanCard from './SubscriptionPlanCard.vue'
import BaseLoading from 'components/BaseLoading.vue'

export default defineComponent({
	name: 'SubscriptionPlan',
	components: {
		SubscriptionPlanCard,
		BaseLoading
	},
	props: {
		plans: {
			type: Array
		},
		loading: {
			type: Boolean,
			default: false
		},
		loadingButton: {
			type: Boolean,
			default: false
		},
		subscriptionOption: {
			type: String
		}
	},
	emits: ['selectPlan', 'update:subscriptionOption'],
	setup(props, { emit }) {
		const { subscriptionOptions } = usePlan()
		const selectedSubscriptionOption = computed({
			get: () => props.subscriptionOption || 'monthly',
			set: (value) => emit('update:subscriptionOption', value)
		})
		const currentPlans = computed(() => {
			return (props.plans || []).map((plan) => ({
				...plan
			}))
		})
		return {
			currentPlans,
			selectedSubscriptionOption,
			subscriptionOptions
		}
	}
})
</script>
<style lang="scss" scoped>
.subscription-plan {
	@apply grid grid-cols-1 md:grid-cols-3 gap-6;
}
</style>