<template>
  <Popover :visible="isVisible" placement="right-end">
    <div class="w-full h-full flex justify-center items-center">
      <span class="font-medium text-teal-04">{{ route.label }}</span>
    </div>
    <template #reference>
      <li
        :class="{
          'mt-auto': route.position,
          hidden: !checkMailIn(route)
        }"
        v-bind="{
          ...(content.main_menu.includes(route.name) && { whitelabel: 'remove' })
        }"
        @mouseenter="isVisible = showTooltip && !!!route.url"
        @mouseleave="isVisible = false"
      >
        <router-link
          v-if="!!!route.url"
          :to="{ name: route.name }"
          @click="menuClicked"
          class="hover:bg-grey-03 text-grey-03 hover:text-white transition-colors whitespace-nowrap"
        >
          <span
            class="h-10 flex items-center justify-center w-6"
            :class="{ 'mx-0': isAlias, 'mx-3': !isAlias }"
          >
            <SVGLoader
              :name="`/menu-icon/${route.icon}-fill`"
              id="buyback-icon"
              class="w-6 h-6"
            />
          </span>
          {{ !isAlias ? route.label : '' }}
        </router-link>
        <Popover
          v-else
          trigger="hover"
          class="flex"
          width="400"
          placement="right-end"
        >
          <div class="my-auto space-y-5">
            <div v-for="(child, i) in route.children" :key="i">
              <div v-if="child.name == 'layoutCallSupprt'">
                <hr class="border-grey-01" />
                <div class="flex">
                  <div class="">
                    <a
                      :href="child.url"
                      class="flex"
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <SVGLoader
                        :name="`/menu-icon/${child.icon}-fill`"
                        id="buyback-icon"
                        class="w-5 h-5 mr-3"
                      />
                      {{ child.label }}
                    </a>
                  </div>
                  <div v-if="isOnlineSupport" class="flex align-items">
                    <img
                      src="@/assets/icon/circle-on.svg"
                      class="w-4 ml-2 absolute"
                      alt="on"
                    />
                    <span class="ml-6">Online</span>
                  </div>
                  <div v-else class="flex">
                    <img
                      src="@/assets/icon/circle-off.svg"
                      class="w-4 ml-2 absolute"
                      alt="on"
                    />
                    <span class="ml-6">Offline</span>
                  </div>
                </div>
                <div
                  class="rounded-md p-4 my-2"
                  style="background-color: #e2e2e2"
                >
                  <p class="font-bold my-2">Opening Hours</p>
                  <p class="flex my-2">
                    <span class="mr-2">Monday - Thursday</span>
                    <span class="ml-2">10AM - 2PM CST</span>
                  </p>
                  <p class="font-bold my-2">Phone Number</p>
                  <p class="my-2">(314) 270-2734</p>
                </div>
              </div>
              <div v-else-if="child.label === 'Help Center'">
                <a class="flex" href="" @click.prevent="handleOpenIntercom">
                  <SVGLoader
                    :name="`/menu-icon/${child.icon}-fill`"
                    id="buyback-icon"
                    class="w-5 h-5 mr-3"
                  />
                  {{ child.label }}</a
                >
              </div>
              <div v-else>
                <a
                  :href="child.url"
                  class="flex"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <SVGLoader
                    :name="`/menu-icon/${child.icon}-fill`"
                    id="buyback-icon"
                    class="w-5 h-5 mr-3"
                  />
                  {{ child.label }}
                </a>
              </div>
            </div>
            <div v-if="false" class="w-full border-t border-grey-01"></div>
            <div v-if="false" class="text-teal-04">
              App Version:{{ ' ' }}
              <a
                whitelabel="remove"
                href="https://help.reusely.com/en/articles/5832380-app-version"
                target="_blank"
                rel="noreferrer noopener"
              >
                {{ me.version }}
              </a>
            </div>
          </div>
          <template #reference>
            <a class="hover:bg-grey-03 text-grey-03 hover:text-white transition-colors whitespace-nowrap">
              <span
                class="h-10 flex items-center justify-center w-5"
                :class="{ 'mx-0': isAlias, 'mx-3': !isAlias }"
              >
                <SVGLoader
                  :name="`/menu-icon/${route.icon}-fill`"
                  id="buyback-icon"
                  class="w-5 h-5"
                />
              </span>
              {{ !isAlias ? route.label : '' }}
            </a>
          </template>
        </Popover>
        <hr v-if="route.meta?.divider" class="border border-grey-03">
      </li>
    </template>
  </Popover>
</template>

<script>
import { defineComponent, toRef, computed, ref } from 'vue'
import { useUser } from 'composable/user'
import { content } from 'constants/whiteLabels'
import SVGLoader from 'components/SVGLoader.vue'

export default defineComponent({
  name: 'MenuItem',
  components: {
    SVGLoader
  },
  props: {
    route: {
      type: Object,
      default: () => ({})
    },
    alias: {
      type: Boolean,
      default: false
    },
    showTooltip: {
      type: Boolean,
      default: false
    }
  },
  emits: ['menu:click'],
  setup(props, { emit }) {
    const { me } = useUser()
    const route = toRef(props, 'route')
    const isAlias = toRef(props, 'alias')
    const isVisible = ref(null)
    const childNumber = computed(() => {
      if (props.route.children) {
        const childVisible = props.route.children.filter(
          (item) => item.meta && item.meta.hidden === false
        )
        return childVisible.length
      }
      return 0
    })
    const onlyChild = computed(() => {
      if (childNumber.value > 1) return null
      if (props.route.children) {
        for (const child of props.route.children) {
          if (!child.meta || !child.meta.hidden) return child
        }
      }
      // return { ...props.route, path: '' }
      return false
    })
    const checkMailIn = (item) => {
      if (item.label !== 'Mail-In') {
        return true
      }

      if (item.label === 'Mail-In' && me.value.is_location_mail_in !== 0) {
        return true
      }

      return false
    }
    const alias = (title) => {
      return title
        .split(' ')
        .map((word) => word[0].toUpperCase())
        .join('')
    }
    const menuClicked = () => {
      emit('menu:click', true)
    }

    const isOnlineSupport = computed(() => {
      const dateChicago = new Date(
        new Date().toLocaleString('en-US', { timeZone: 'America/Chicago' })
      )
      const dayOnline = 1 <= dateChicago.getDay() && dateChicago.getDay() <= 4
      const timeOnline =
        10 <= dateChicago.getHours() && dateChicago.getHours() <= 14

      if (dayOnline && timeOnline) {
        return true
      } else {
        return false
      }
    })
    const handleOpenIntercom = () => {
      window.CustomerSupport.use('show')
    }
    return {
      me,
      route,
      content,
      onlyChild,
      isAlias,
      isVisible,
      alias,
      menuClicked,
      checkMailIn,
      handleOpenIntercom,
      isOnlineSupport
    }
  }
})
</script>
