<script>
import { defineComponent, h, ref } from 'vue'
import { mixinColor } from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'

export default defineComponent({
  name: 'Alert',
  mixins: [
    ...addMixinPrefix([
      mixinColor,
    ], 'avatar')
  ],
  props: {
    text: {
      type: Boolean,
      default: false
    },
    image: {
      type: String,
      default: ''
    }
  },
  setup(props, { slots }) {
    const genText = () => {
      return slots.default && slots.default() || ''
    }
    const genImage = () => {
      return {
        'background-image': `url(${props.image})`
      }
    }
    return {
      genText,
      genImage
    }
  },
  render() {
    const children = [this.$props.text && this.genText()]
    const listClass = [
      'avatar',
      this.classColor
    ]

    return h('div', {
      class: listClass,
      style: this.genImage(),
    }, children)
  }
})
</script>