<script setup async>
import { computed, defineProps, ref, watch } from 'vue'
import { tryCatchRequest } from 'utils/request'
import { useMultipleCompany } from 'store/pinia/layouts/multiple-company'

import BaseLoading from 'components/BaseLoading.vue'
import TextLimit from 'components/TextLimit.vue'
import MultipleCompanyDetails from './MultipleCompanyDetails.vue'
import MultipleCompanyUnlink from './MultipleCompanyUnlink.vue'

const store = useMultipleCompany()
const dropdownCompanies = ref()
const props = defineProps({
  mobile: {
    type: Boolean,
    default: false
  }
})

const onShowDetail = async (company) => {
  dropdownCompanies.value.visible = false
  store.companyDetail.token = company.company_token
  store.companyDetail.modal = true
}
const onSelect = async (n) => {
  if (!companies.value[n].company_token || companies.value[n].is_selected) return

  await tryCatchRequest(
    store.company,
    () => store.$switchCompany({
      token: localStorage.getItem('token'),
      company: companies.value[n].company_token
    })
  ).then(() => {
    window.location.href = '/dashboard'
  })
}

await tryCatchRequest(store.company, () => store.$getBulkMultipleCompany())

const activeCompanyName = ref(store.activeCompany.value)
const companies = computed(() => store.companies.value)
const isGhost = computed(() => store.isGhost.value)

watch(
  () => companies.value,
  () => {
    activeCompanyName.value = companies.value.find(item => item.is_selected)?.name
  },
  { immediate: true, deep: true })
</script>

<template>
  <Suspense>
    <template #fallback>
      <div class="flex items-center justify-center mb-10">
        <BaseLoading />
      </div>
    </template>
    <div>
      <div v-loading="store.company.isLoading">
        <Dropdown
          ref="dropdownCompanies"
          :hide-on-click="true"
          :placement="props.mobile ? 'right-start' : 'bottom-start'"
          trigger="click"
          size="medium">
          <div
            :class="[
              'company-trigger',
              'flex items-center gap-x-2',
              props.mobile
                ? 'text-white justify-center company-trigger--mobile'
                : 'justify-between hover:bg-grey-lighten p-4 rounded-lg'
            ]">
            <TextLimit
              :text="`${activeCompanyName}` || '-'"
              tooltip-placement="bottom-start"
              class="w-52">
              <template #default="{ text }">
                <h1
                  :class="[ mobile ? 'text-white' : 'text-teal-04']"
                  class="w-full font-medium text-lg truncate py-1">
                  {{ text }}
                </h1>
              </template>
            </TextLimit>
            <i class="material-icons icon-sm text-sm">expand_more</i>
          </div>
          <template #dropdown>
            <DropdownMenu>
              <DropdownItem
                v-for="(company, n) in companies"
                :key="n">
                <div
                  :class="[
                    'company-item text-teal-04',
                    company.is_selected && 'company-item--active'
                  ]"
                  @click="onSelect(n)">
                  <div class="flex items-center gap-x-4">
                    <TextLimit
                      :text="company.name || '-'"
                      tooltip-placement="top"
                      class="max-w-md">
                      <template #default="{ text }">
                        <p class="w-full">{{ text }}</p>
                      </template>
                    </TextLimit>
                    <div
                      v-if="company.text"
                      :class="[
                        company.class_alert,
                        'flex items-center gap-x-2 h-7 px-4 rounded-full'
                      ]">
                      <div
                        :class="[
                          company.class_dot,
                          'h-3 w-3 rounded-full'
                        ]" />
                      <span
                        :class="[
                          company.class_text,
                          'text-xs font-normal'
                        ]">
                        {{ company.text }}
                        <b v-if="company.days">{{ company.days }} Days</b>
                      </span>
                    </div>
                  </div>
                  <a 
                    href="#"
                    class="company-badge" 
                    @click.stop="onShowDetail(company)">
                    <i class="material-icons text-teal-03">settings</i>
                  </a>
                </div>
              </DropdownItem>
              <DropdownItem v-if="!isGhost && store.isMultipleAccount">
                <router-link
                  to="/new-company"
                  class="company-item border-t border-grey-01 border-opacity-50">
                  <div class="text-grey-base relative">
                    <i class="material-icons">add_circle_outlined</i>
                    <span class="-ml-4">
                      New Account
                    </span>
                  </div>
                </router-link>
              </DropdownItem>
            </DropdownMenu>
          </template>
        </Dropdown>
      </div>
      <MultipleCompanyDetails />
      <MultipleCompanyUnlink />
    </div>
  </Suspense>
</template>


<style lang="scss" scoped>
.company-trigger {
  min-width: 120px;

  &--mobile {
    min-width: unset;
  }
}
.company-item {
  min-width: 400px;
  margin-left: -17px;
  margin-right: -17px;
  padding: .5rem 0 .5rem 0;
  @apply flex items-center justify-between gap-x-2 px-4 capitalize;
  .company-badge {
    @apply hidden;
  }

  &--active {
    @apply font-medium;
  }

  &:hover {
    .company-badge {
      @apply text-sm text-grey-base hover:text-grey-darken font-semibold flex items-center justify-center;
      width: 30px;
      height: 30px;
      border-radius: 8px;
      padding: 0;
      
      &:hover {
        @apply bg-grey-01;
      }
      
      i {
        margin: 0;
      }
    }
  }
}
</style>
