<template>
  <div class="h-full w-full border-2 border-grey-01 py-3 rounded-md text-center flex items-center justify-center">
    <Dropdown :hide-on-click="true" trigger="click" placement="bottom" size="medium">
      <div class="flex items-center justify-center">
        <span class="material-icons text-base lg:mr-2"> language </span>
        <span class="text-base lg:inline-block hidden"> New Version </span>
      </div>
      <template #dropdown>
        <DropdownMenu>
          <DropdownItem v-for="item in datas" :key="item.label" @click="handleRedirect(item)" :disabled="item.label === 'New Version'" class="hover:bg-grey-lighten">
            <div>
              <span class="material-icons text-lg text-teal-04" :class="{ 'opacity-0': item.label !== 'New Version' }">check_circle</span>
              <span class="mx-5 text-sm">{{ item.label }}</span>
            </div>
          </DropdownItem>
        </DropdownMenu>
      </template>
    </Dropdown>
  </div>
</template>

<script>
import { defineComponent } from "vue";

export default defineComponent({
  setup() {
    let token;
    try {
      token = window.localStorage.getItem("token");
    } catch (e) {
      token = "";
    }

    const datas = [
      {
        label: "New Version",
        link: "currentUrl",
      },
      {
        label: "Old Version",
        link: `https://legacy-app.buyback.ai/login?token=${token}`,
      },
    ];

    const handleRedirect = (e) => {
      if (e.link !== "currentUrl") {
        window.location.href = e.link;
      }
    };

    return { datas, handleRedirect };
  },
});
</script>
