<template>
  <div 
    @click="handleShowModal"
    class="px-6 py-4 hover:bg-grey-lighten space-y-2 border-b border-grey-01 cursor-pointer">
    <div class="flex justify-between items-center">
      <p class="text-grey-02 text-sm flex items-center">
        <i class="material-icons mr-2" :class="getCategoryIcon(notification.type_notification).color">
          {{ getCategoryIcon(notification.type_notification).icon }}
        </i>
        {{ getCategoryTitle(notification.type_notification) }} &bull; {{ notification.created_at }}
      </p>
      <div class="relative" ref="dropdownContainerRef">
        <button @click.stop="toggleDropdown" class="btn-action">
          <span class="material-icons">more_horiz</span>
        </button>
        <div v-if="isDropdownOpen" 
             class="absolute right-0 mt-1 py-1 w-48 bg-white rounded-md shadow-lg z-50 border border-grey-01">
          <button 
            @click.stop="handleChangeStatus(notification)"
            class="flex items-center w-full px-4 py-2 text-left hover:bg-grey-01">
            <span class="material-icons mr-2">
              {{ notification.mark_as_read == 0 ? 'mark_email_unread' : 'drafts' }}
            </span>
            <span class="text-sm">
              {{ notification.mark_as_read == 0 ? 'Mark as read' : 'Mark as unread' }}
            </span>
          </button>
        </div>
      </div>
    </div>
    <h2 class="text-base" :class="notification.mark_as_read == 0 ? 'font-semibold' : 'font-normal'">{{ notification.title }}</h2>
    <p class="text-grey-02 leading-6 clamp-2">{{ notification.description }}</p>
  </div>

  <NotificationModalDetail v-model="showModal" title="Message Info" :data="notificationDetail" @onClose="handleMarkAsRead" :loading="loadingShowDetail" />
</template>

<script setup>
import { defineProps, defineEmits, ref, onMounted, onUnmounted } from 'vue'
import NotificationModalDetail from './NotificationModalDetail.vue'

const props = defineProps({
  notification: {
    type: Object,
    required: true
  },
  notificationDetail: {
    type: Object,
    default: () => ({})
  },
  loadingShowDetail: {
    type: Boolean,
    default: false
  }
})

const emit = defineEmits(['change-status', 'show-detail', 'mark-as-read'])

const showModal = ref(false)
const isDropdownOpen = ref(false)
const dropdownContainerRef = ref(null)

const handleShowModal = () => {
  showModal.value = true
  emit('show-detail')
}

const toggleDropdown = (event) => {
  event.stopPropagation()
  isDropdownOpen.value = !isDropdownOpen.value
}

const handleMarkAsRead = () => {
  emit('mark-as-read')
  isDropdownOpen.value = false
}

const handleChangeStatus = () => {
  emit('change-status')
  isDropdownOpen.value = false
}

const handleClickOutside = (event) => {
  if (isDropdownOpen.value && 
      dropdownContainerRef.value && 
      !dropdownContainerRef.value.contains(event.target)) {
    isDropdownOpen.value = false
  }
}

onMounted(() => {
  document.addEventListener('mousedown', handleClickOutside)
})

onUnmounted(() => {
  document.removeEventListener('mousedown', handleClickOutside)
})

const getCategoryTitle = (category) => {
  const titles = {
    info: 'Info',
    failure: 'Failure',
  }
  return titles[category] || 'Notification'
}

const getCategoryIcon = (category) => {
  const icons = {
    info: { icon: 'info', color: 'text-blue-04' },
    failure: { icon: 'warning', color: 'text-orange-04' },
  }
  return icons[category] || { icon: 'notifications', color: 'text-grey-02' }
}
</script>

<style scoped lang="scss">
.clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.btn-action {
  @apply rounded-lg hover:bg-grey-01 hover:border-grey-03 w-8 h-8;
  &:hover {
    border: 1px solid;
  }
}
</style>