<template>
  <Dropdown
  ref="dropdownRef" 
  trigger="click" 
  :hide-on-click="false" 
  size="large"
  :popper-options="{
    modifiers: [
      {
        name: 'preventOverflow',
        options: {
        padding: 40,
        },
      },
    ],
  }"
  >
    <button @click="handleShowNotification" class="rounded-lg hover:bg-grey-lighten w-12 h-12">
      <div v-if="isUnread" class="relative">
        <span class="material-symbols-outlined icon-md text-grey-02">notifications</span>
        <div class="absolute top-0 right-3 w-3 h-3 bg-red-base rounded-full border-2 border-white"></div>
      </div>
      <span v-else class="material-symbols-outlined icon-md text-grey-02">notifications</span>
    </button>
    <template #dropdown>
      <DropdownMenu class="dropdown">
        <div class="py-4 px-6 border-b border-grey-01 flex items-center justify-between">
          <h2 class="font-semibold text-xl">Notifications</h2>
          <Button v-if="isUnread" dense color="teal" @click="markAllAsRead" :loading="loading.markAllAsRead">Mark all read</Button>
        </div>
        <div class="pt-4 px-6">
          <Tab 
            :items="tab" 
            by="value" 
            name="label" 
            component 
            color="teal" 
            view="smooth" 
            dense 
            v-model="selectedTab"
          />
        </div>
        <div class="max-h-96 overflow-y-auto">
          <div v-if="loading.show" class="w-full h-96 flex items-center justify-center">
            <Loading />
          </div>
          <template v-if="notificationState?.data?.length > 0">
            <NotificationItem 
              v-for="notification in notificationState?.data" 
              :key="notification.id"
              :notification="notification"
              :notificationDetail="notificationDetail"
              @show-detail="handleGetDetail(notification)"
              @change-status="toggleReadStatus(notification)"
              @mark-as-read="handleMarkAsRead(notification)"
              :loadingShowDetail="loading.showDetail"
            />
          </template>
          <div v-if="notificationState?.data?.length === 0 && !loading.show" class="p-8 text-center text-grey-02">
            <h1 class="font-semibold text-lg">Youre all caught up!</h1>
            <p class="mt-2 text-sm text-grey-02">There are no new notifications at this time.</p>
          </div>
        </div>
        <div class="p-6 flex items-center justify-between">
          <p v-if="notificationState?.data?.length > 0 && selectedTab === 'all'" class="text-grey-02">
            {{ notificationState?.data?.length }} of {{ notificationState?.total }} notifications
          </p>
          <p v-if="notificationState?.data?.length > 0 && selectedTab === 'unread'" class="text-grey-02">
            {{ notificationState?.data?.length }} of {{ notificationState?.total_unread_notification }} notifications
          </p>
          <Button 
            class="ml-auto" 
            view="outlined" 
            dense 
            color="teal" 
            :link="true" 
            :to="{ name: 'notification' }" 
            @click="closeDropdown">View all notifications</Button>
        </div>
      </DropdownMenu>
    </template>
  </Dropdown>
</template>

<script setup>
import { ref, watch, onMounted, computed } from 'vue'
import { useRouter } from 'vue-router'
import Button from 'bbUi/components/button/button.vue'
import Tab from 'bbUi/components/tab/tab.vue'
import NotificationItem from './NotificationItem.vue'
import { useStore } from 'vuex'
import { useUser } from 'composable/user'
import { useNotification } from 'composable/notification'
import Loading from 'bbUi/components/loading/loading.vue'
import { ElMessage } from 'element-plus'

const router = useRouter()
const store = useStore()
const { me } = useUser()
const { 
  tab,
  filter,
  loading,
  actionShow, 
  notificationState, 
  notificationDetail,
  actionShowDetail, 
  actionMarkAsRead, 
  actionMarkAsUnread, 
  actionMarkAllAsRead 
} = useNotification()

const selectedTab = ref('unread')
const dropdownRef = ref(null)
const hasLoadedNotification = ref(false)

const isUnread = computed(() => {
  return store.state.notification.isHasUnreadNotification !== null 
    ? store.state.notification.isHasUnreadNotification 
    : me.value.has_unread_notification
})

onMounted(() => {
  setupRouteWatcher()
})

const closeDropdown = () => {
  if (dropdownRef.value) {
    if (typeof dropdownRef.value.handleClose === 'function') {
      dropdownRef.value.handleClose()
    } 
    else if (dropdownRef.value.hide && typeof dropdownRef.value.hide === 'function') {
      dropdownRef.value.hide()
    }
    else if (hasOwnProperty(dropdownRef.value, 'visible')) {
      dropdownRef.value.visible = false
    }
  }
}

const handleGetDetail = async (notification) => {
  await actionShowDetail({ id: notification.id })
}

const toggleReadStatus = async (notification) => {
  const payload = { ids: [notification.id] }
  if (notification.mark_as_read == 0) {
    await actionMarkAsRead(payload)
  } else {
    await actionMarkAsUnread(payload)
  }
  ElMessage({
    type: 'success',
    message: `Notification have been marked as ${notification.mark_as_read == 0 ? 'read' : 'unread'}!`
  })

  await handleShowNotification()
}

const handleMarkAsRead = async (notification) => {
  if (notification.mark_as_read == 0) {
    await actionMarkAsRead({
      ids: [notification.id]
    })

    await handleShowNotification()
  }
}

const handleShowNotification = async () => {
  const payload = { ...filter }

  payload.mark_as_read = selectedTab.value === 'unread' ? 0 : 1

  await actionShow(payload)
  hasLoadedNotification.value = true
}

const markAllAsRead = async () => {
  try {
    await actionMarkAllAsRead({ mark_as_read: 0 })
    await handleShowNotification()
    ElMessage({
      type: 'success',
      message: 'All notifications have been marked as read!'
    })
    selectedTab.value = 'all'
  } catch (error) {
    console.log(error)
    ElMessage({
      type: 'error',
      message: error.message
    })
  }
}

const setupRouteWatcher = () => {
  router.afterEach(() => {
    if (hasLoadedNotification.value) {
      handleShowNotification()
    }
  })
}

watch(() => ({ ...filter }), () => {
  if (hasLoadedNotification.value) {
    handleShowNotification()
  }
})

watch(selectedTab, () => {
  if (hasLoadedNotification.value) {
    handleShowNotification()
  }
})

watch(() => (store.state.notification.isHasUnreadNotification), (newValue) => {
  if (newValue == 1 && selectedTab.value === 'unread') {
    handleShowNotification()
  }
})

function hasOwnProperty(obj, prop) {
  return Object.prototype.hasOwnProperty.call(obj, prop)
}
</script>




<style scoped>
.dropdown {
  width: 450px;
  display: block;
}

.el-dropdown__popper .el-dropdown-menu.el-dropdown-menu--large.dropdown {
  max-height: none !important;
  height: max-content !important;
}
</style>