<template>
    <div class="el-loading-spinner spinner">
        <svg class="circular" viewBox="25 25 50 50">
            <circle class="path" cx="50" cy="50" :r="sizes" fill="none"></circle>
        </svg>
    </div>
</template>
<script>
import {defineComponent} from 'vue'
export default defineComponent({
    props: {
        size: {
            type: String,
            default: 'md'
        }
    },
    computed: {
        sizes() {
            if(this.size == 'lg') return 20
            else if(this.size == 'md') return 15
            else return 10
        }
    }
})
</script>
<style scoped>
.spinner{
    position: relative;
    top: 0;
    left: 0;
    margin-top: 0;
    width: unset;
}
</style>