<template>
  <div id="userAgentHandler" class="fixed w-full h-full bg-black bg-opacity-30 z-50" :class="{ 'block' : !validUserAgent, 'hidden' : validUserAgent }">
    <div class="flex absolute left-1/2 top-1/2 transform -translate-y-1/2 -translate-x-1/2 bg-white w-3/6 lg:w-2/6 h-2/6 rounded-md px-4">
      <div class="mx-auto my-auto space-y-6 w-full text-center">
        <div class="mx-auto text-xl">Upgrade browser for full Buyback experience</div>
        <div class="flex">
          <div class="flex mx-auto space-x-8">
            <a href="https://www.mozilla.org/" target="_blank" rel="noopener noreferrer">
              <img src="/icon/browser/firefox.png" class="w-14 h-14">
            </a>
            <a href="https://www.google.com/chrome/" target="_blank" rel="noopener noreferrer">
              <img src="/icon/browser/chrome.png" class="w-14 h-14">
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="h-screen overflow-x-hidden overflow-y-auto">
    <div v-if="route" class="flex flex-col h-full overflow-y-hidden">
      <div class="flex w-full h-full relative">
        <transition name="fade">
          <MobileSidebar v-if="isActiveMobileSidebar && !isTrial && !isUnpaidBilling" @closed="isActiveMobileSidebar = false" :is-mailin="isMailinLocation" />
        </transition>
        <div :class="{
          '': route.name === 'endTrial',
          'sidebar sidebar--active': isActiveSidebar && !isTrial && !isUnpaidBilling,
          'sidebar': !isActiveSidebar && !isTrial && !isUnpaidBilling
        }">
          <div v-if="!isTrial && !isUnpaidBilling" class="h-full" :class="['sidebar__menus', !isActiveSidebar && 'sidebar--closed']">
            <div class="h-full flex flex-col justify-between">
              <ul v-for="(option, keyOption) in options" :key="keyOption">
                <template v-if="keyOption === 0">
                  <MenuItem v-for="item in option" :route="item" :key="item.name" :alias="!isActiveSidebar" :show-tooltip="!isActiveSidebar" />
                </template>
                <template v-else-if="keyOption === 1">
                  <MenuItem v-for="item in option" :route="item" :key="item.name" :alias="!isActiveSidebar" :show-tooltip="!isActiveSidebar" />
                  <li class="h-10" />
                </template>
              </ul>
            </div>
          </div>
        </div>
        <div v-if="currentRouteHasStackedNav" :class="$style['stacked-nav']">
          <h2 class="text-2xl p-8">{{currentParentRoute.meta?.title}}</h2>
          <div
            v-for="child in currentParentRouteChildren"
            :key="child"
            @click="() => !child.is_disabled && $router.push({ name: child.name })"
            :class="[
              $style['stacked-nav__menu'],
              child.name === route.name
                ? $style['stacked-nav__menu--active']
                : '',
              child.is_disabled
                ? $style['stacked-nav__menu--disabled']
                : ''
            ]"
          >
            {{ child.meta.title }}
          </div>
        </div>
        <div class="w-full" :class="['content', isActiveSidebar && 'content--active' || '']">
          <div ref="refNavbar" class="sticky top-0 left-0 right-0 bg-white" style="z-index: 1999;">
            <div :class="['navbar', 'py-4', isActiveSidebar && 'navbar--active' || '']">
              <div class="flex items-center justify-between">
                <div class="navbar__left w-full mr-6 flex items-center">
                  <div class="navbar__brand lg:block md:block hidden">
                    <div class="lg:block hidden mx-8">
                      <Suspense>
                        <MultipleCompany />
                      </Suspense>
                    </div>
                    <div class="xl:hidden lg:hidden block cursor-pointer">
                      <span class="material-icons icon-md mx-7" @click="isActiveMobileSidebar = !isActiveMobileSidebar">{{ isActiveMobileSidebar && 'close' || 'menu'  }}</span>
                    </div>
                  </div>
                  <div v-if="isCanceledSubscription" class="flex space-x-3 bg-orange-01 px-4 py-1 rounded-full ml-10">
                    <span class="w-3 h-3 rounded-full bg-orange-base my-auto"></span>
                    <div class="text-orange-base">Subscription Canceled</div>
                  </div>
                  <Popover v-if="isShowTrialEndWarning" trigger="hover" width="400">
                    <p class="text-center">
                      Your free trial ends on
                      <span class="font-semibold">{{ formattedTrialEndDate }}</span
                      >. To continue using <span whitelabel="text-replace" data-label="addons host">Reusely.com</span> <br />
                      after the trial date make sure you add your card in billing.
                    </p>
                    <template #reference>
                      <div class="flex space-x-3 bg-orange-01 px-4 py-1 rounded-full ml-10 cursor-pointer" @click="handleToPlansAndBill">
                        <span class="w-3 h-3 rounded-full bg-orange-04 my-auto"></span>
                        <div class="text-orange-04">Trial ends on <span class="font-semibold">{{ formattedTrialEndDate }}</span></div>
                      </div>
                    </template>
                  </Popover>
                  <div v-if="!isTrial && !isUnpaidBilling" class="lg:flex items-center flex-1 lg:ml-5 lg:pr-16">
                    <div class="mr-5 lg:block hidden">
                       <TradeInButton />
                    </div>
                    <div class="search flex-1">
                      <AutoComplete placeholder="Search everything" by="box_id" :items="data.data" :clearOnSelect="true" :loading="isSearchLoading" :flat="true" :rounded="true" @select="selectedOrder" @change="changeSearch" @focus-in="isHideNavMenu = isMobile ? true : isHideNavMenu" @focus-out="isHideNavMenu = isMobile ? false : isHideNavMenu" :clearable="true">
                        <template #loading>
                          <div class="flex items-center h-20">
                            <Spinner size="sm" />
                            <span class="ml-3">Please wait...</span>
                          </div>
                        </template>
                        <template #default="{item}">
                          <div class="flex items-center justify-between gap-x-3">
                            <div class="flex items-center gap-x-3">
                              <Tooltip v-if="!item.data.my_location_order" placement="top">
                                <template #content>
                                  <div class="w-52 text-center">You don not have permission to view offers at this location</div>
                                </template>
                                <i class="material-icons" style="color: orange;">warning</i>
                              </Tooltip>
                              <div>
                                #{{ item.data.box_id }} - {{ `${item.data.customer.first_name} ${item.data.customer.last_name ? item.data.customer.last_name : '' }`}}
                              </div>
                              <div
                                :class="[
                                  item.data.type === 'Local Buyback' ? 'bg-blue-01 text-blue-03' : 'bg-green-01 text-green-03',
                                  'rounded-full whitespace-nowrap px-4 py-1'
                                ]">
                                {{ item.data.type === 'Local Buyback' ? 'In-Store' : 'Mail-In' }}
                              </div>
                            </div>
                            <div v-if="item.data.type === 'Local Buyback'" class="flex items-center">
                              <i class="material-icons text-grey-03 mr-2">location_on</i>
                              {{ item.data.location.name }}
                            </div>
                          </div>
                        </template>
                      </AutoComplete>
                    </div>
                  </div>
                  <div v-else class="ml-6">
                    <div v-if="isUnpaidBilling" class="flex space-x-3 bg-red-01 px-4 py-1 rounded-full">
                      <span class="w-3 h-3 rounded-full bg-red-base my-auto"></span>
                      <div class="text-red-base">Unpaid Billing</div>
                    </div>
                    <div v-else class="flex space-x-3 bg-red-01 px-4 py-1 rounded-full">
                      <span class="w-3 h-3 rounded-full bg-red-base my-auto"></span>
                      <div class="text-red-base">Trial Expired</div>
                    </div>
                  </div>
                </div>
                <transition name="fade">
                  <div class="navbar__right" v-if="!isHideNavMenu">
                    <div :class="['navbar__items', isActiveSidebar && 'navbar--active' || '']">
                      <div class="flex items-center lg:justify-between justify-end">
                        <div class="flex items-center gap-x-5 ml-6 mr-5">
                          <div v-if="false" class="sm:block hidden lg:w-60 w-12">
                            <SwitchLocalVersion />
                          </div>
                          <div v-if="false" class="sm:block hidden lg:w-60 w-12">
                            <SwitchCompany />
                          </div>
                          <div class="w-60">
                            <SwitchLocation ref="switcherLocation" :disabled="isTrialEnd == 1 || isUnpaidBilling" />
                          </div>
                          <NotificationDropdown />
                          <div class="w-px bg-grey-01 mr-3 flex-shrink-0 self-stretch"></div>
                          <Dropdown trigger="click" :hide-on-click="true" placement="bottom" size="medium">
                            <div class="profile">
                              <div class="profile__content flex justify-center items-center">
                                <Avatar text color="teal" class="mr-4">{{ aliasName }}</Avatar>
                                <div class="flex flex-col xl:block lg:block hidden">
                                  <h6 class="mr-2 mb-2 text-lg font-semibold">{{ me.first_name }} {{ me.last_name }}</h6>
                                  <p class="text-sm text-grey-04 mb-2" v-if="me.role">as {{ me.role }}</p>
                                  <p class="text-base text-grey-04" v-if="me.role && me.role === 'Super Admin'">
                                    {{ me.company_name }}
                                  </p>
                                </div>
                                <span class="material-icons icon-sm text-sm ml-2">expand_more</span>
                              </div>
                            </div>
                            <template #dropdown>
                              <DropdownMenu>
                                <DropdownItem v-if="!isTrial" @click="handleMySettings"><span class="mx-5">My Settings</span></DropdownItem>
                                <DropdownItem v-if="!isTrial" @click="modalChangePassword = true"><span class="mx-5">Change Password</span></DropdownItem>
                                <DropdownItem @click="logout"><span class="mx-5">Logout</span></DropdownItem>
                              </DropdownMenu>
                            </template>
                          </Dropdown>
                        </div>
                      </div>
                    </div>
                  </div>
                </transition>
              </div>
            </div>
          </div>

          <div :class="[isNoPaddingContent ? 'relative h-full' : 'p-6']" :style="{minHeight: contentMinHeight}">
            <router-view v-slot="{ Component }">
              <component
                :is="Component"
                :key="
                  $route.meta?.key
                    ? $route.meta.key === 'path' && $route.fullPath
                    : $route.matched[0]
                "
              ></component>
            </router-view>
          </div>
        </div>
      </div>
    </div>
  </div>
  <Modal v-model="modalProfile" title="Edit Profile">
    <div class="grid grid-cols-1">
      <div class="mb-5">
        <Input v-model="dataProfile.first_name" label="First Name" :view="validationProfile.first_name.$invalid && 'danger' || 'default'" block />
        <span class="text-red-base mt-2 block" v-for="(error, index) of validationProfile.first_name.$errors" :key="`${index}${error}`">{{ error.$message }}. </span>
      </div>
      <div class="mb-5">
        <Input v-model="dataProfile.last_name" label="Last Name" :view="validationProfile.last_name.$invalid && 'danger' || 'default'" block />
        <span class="text-red-base mt-2 block" v-for="(error, index) of validationProfile.last_name.$errors" :key="`${index}${error}`">{{ error.$message }}. </span>
      </div>
      <div class="mb-5">
        <Input v-model="dataProfile.email" type="email" label="Email Address" :view="validationProfile.email.$invalid && 'danger' || 'default'" block />
        <span class="text-red-base mt-2 block" v-for="(error, index) of validationProfile.email.$errors" :key="`${index}${error}`">{{ error.$message }}. </span>
      </div>
    </div>
    <template #footer>
      <Button view="smooth" color="teal" :loading="loadingProfile" @click="profileUpdate">Save</Button>
    </template>
  </Modal>
  <Modal v-model="modalChangePassword" title="Change Password">
    <template #default>
      <ChangePassword :data="dataChangePassword" ref="refChangePassword" />
    </template>
    <template #footer>
      <Button color="teal" :loading="loadingChangePassword" @click="passwordUpdate">Save</Button>
    </template>
  </Modal>
  <!-- <button
    v-if="intercomLauncher.launcher"
    id="intercom_launcher"
    :style="{
      zIndex: intercomLauncher.index - 1000
    }"
    class="fixed right-7 bottom-4 bg-teal-04 rounded-full h-20 w-20">
    <div class="w-full h-full relative flex items-center justify-center">
      <img
        src="/loading/page-loading/intercom-bubble.svg"
        :class="[
          intercomPanel && 'opacity-0 invisible scale-50',
          !intercomPanel && 'opacity-100 visible scale-100'
        ]"
        class="absolute left-1/2 top-1/2 transform -translate-y-1/2 -translate-x-1/2 transition-all ease-in-out duration-300"
      />
      <span
        src="/loading/page-loading/intercom-bubble.svg"
        :style="{
          fontSize: '3.25rem !important'
        }"
        :class="[
          intercomPanel && 'opacity-100 visible scale-100',
          !intercomPanel && 'opacity-0 invisible scale-50'
        ]"
        class="material-icons text-white absolute left-1/2 top-1/2 transform -translate-y-1/2 -translate-x-1/2 transition-all ease-in-out duration-300">
        expand_more
      </span>
    </div>
  </button> -->
</template>

<script>
import { defineComponent, computed, ref, onMounted, watch, onUnmounted, onBeforeMount } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useStore } from 'vuex'
import { useUser, useLogin, useLogout, useProfile, useChangePassword } from 'composable/user'
import { useScanIn, useSearch } from 'composable/dashboard'
import MenuItem from 'components/MenuItem.vue'
import AutoComplete from 'bbUi/components/auto-complete/auto-complete.vue'
import Avatar from 'bbUi/components/avatar/avatar.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Input from 'bbUi/components/input/input.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import MobileSidebar from 'components/dashboard/MobileSidebar.vue'
// import OnBoarding from 'components/OnBoarding.vue'
import Spinner from 'components/Spinner.vue'
import debounce from 'utils/debounce'
import SwitchLocation from 'components/Switch/Location.vue'
import SwitchLocalVersion from 'components/Switch/LocalVersion.vue'
import SwitchCompany from 'components/Switch/Company.vue'
import ChangePassword from 'components/ChangePassword/Form.vue'
import MultipleCompany from 'components/Features/Layouts/MultipleCompany/MultipleCompany.vue'
import SubscriptionPlan from 'components/Subscription/SubscriptionPlan.vue'
import TradeInButton from 'components/TradeInButton.vue'

import { useVuelidate } from '@vuelidate/core'
import { required, email } from '@/validations/index'
import { Userpilot } from 'userpilot'
import { ElMessage } from 'element-plus'
import moment from 'moment'
import { queue } from 'utils/request'
import { isObject } from 'utils/is'
import NotificationDropdown from 'components/Notification/NotificationList/NotificationDropdown.vue'

export default defineComponent({
  components: {
    MenuItem,
    AutoComplete,
    MobileSidebar,
    Avatar,
  // OnBoarding,
    Button,
    Modal,
    Input,
    Alert,
    Spinner,
    SwitchLocation,
    SwitchLocalVersion,
    SwitchCompany,
    ChangePassword,
    MultipleCompany,
    SubscriptionPlan,
    TradeInButton,
    NotificationDropdown
  },
  setup() {
    const switcherLocation = ref()
    const router = useRouter()
    const route = useRoute()
    const store = useStore()
    const { loading: loadingProfile, modal: modalProfile, data: dataProfile, action: actionProfile } = useProfile()
    const { loading: loadingChangePassword, modal: modalChangePassword, data: dataChangePassword, action: actionChangePassword } = useChangePassword()
    const { logout } = useLogout()
    const { scanInData, scanIn } = useScanIn()
    const { data, actionSearch } = useSearch()
    const { me, menu, isTrialEnd, isUnpaidBilling, isCanceledSubscription, isLegacyPlan, isMailinLocation, intercomPanel, intercomLauncher, action: getUser } = useUser()
    const { loadingLoginStatus, loginStatus } = useLogin()

    Object.assign(dataProfile, {
      first_name: me.value.first_name,
      last_name: me.value.last_name,
      email: me.value.email
    })

    const refNavbar = ref(null)
    const validUserAgent = ref(true)
    const refChangePassword = ref(null)
    const isActiveSidebar = ref(false)
    const isActiveMobileSidebar = ref(false)
    const screenWidth = ref(0)
    const isHideNavMenu = ref(false)
    let sessionChecker = null

    const options = computed(() => {
      const exception = ['layoutHelpTOS', 'layoutIntegration', 'layoutSetting']
      const splitMenu = [[], []]
      menu.value.map((item, key) => {
        if (exception.includes(item.name)) {
          splitMenu[1].push(item)
        } else splitMenu[0].push(item)
      })
      return splitMenu
    })
    const routes = computed(() => {
      return store.state.routes
    })
    // const me = computed(() => {
    //   return store.state.user.me
    // })
    const aliasName = computed(() => {
      return `${me.value.first_name} ${me.value.last_name}`.split(' ').map(name => name[0]?.toUpperCase?.()).join('')
    })
    const isMobile = computed(() => screenWidth.value <= 640)
    const currentRouteHasStackedNav = computed(() => route.meta.stackedNav === true)
    const currentParentRoute = computed(() => route.matched.length > 1 ? route.matched[1] : route.matched[0])
    const currentParentRouteChildren = computed(() => {
      const parentRoute = currentParentRoute.value?.children || []
      return parentRoute.map((route) => {
        const isDisabledBillingRoute = route.name === 'settingSubscriptionBilling' && me.value.is_disable_billing
        const isDisabledRoute = isDisabledBillingRoute
        return ({...route, is_disabled: isDisabledRoute})
      })
    })
    const isNoPaddingContent = computed(() => route.meta.noPadding === true)

    const isSearchLoading = ref(false)
    const changeSearch = debounce(async(value) => {
      isSearchLoading.value = true
      data.search = value
      await actionSearch()
      isSearchLoading.value = false
    }, 600)

    const selectedOrder = async (value) => {
      let index = data.data.findIndex(item => item.box_id === value)
      let selectedData = data.data[index]
      if (!selectedData.my_location_order) {
        return ElMessage({
          type: 'warning',
          message: 'You do not have permission to view offers at this location!'
        })
      }
      await handleSwitchLocationSearch(selectedData)
    }

    const isTrial = computed(() => me.value.is_trial_end !== 0)
    const isShowTrialEndWarning = computed(() => me.value.trial_end !== null && !isCanceledSubscription.value && !me.value.is_trial_end)
    const formattedSubscriptionEndDate = computed(() =>
      me.value.subscription_period_end
        ? moment(
            me.value.subscription_period_end,
            'YYYY-MM-DD HH:mm:ss'
          ).format(me.value.date_format)
        : ''
    )
    const formattedTrialEndDate = computed(() => isShowTrialEndWarning.value ? moment(me.value.trial_end, 'YYYY-MM-DD HH:mm:ss').format(me.value.date_format) : '')
    const intercomUserCustomAttributes = computed(() => isObject(me.value.intercom_data) ? me.value.intercom_data : {})
    const contentMinHeight = computed(() => {
      if(!refNavbar.value) return 0
      const navbarHeight = refNavbar.value.getBoundingClientRect().height
      return `calc(100vh - ${navbarHeight}px)`
    })

    const rulesProfile = {
      first_name: { required },
      last_name: { required },
      email: { required, email }
    }
    const validationProfile = useVuelidate(rulesProfile, dataProfile, {
      $lazy: true
    })
    const validateProfile = () => {
      validationProfile.value.$validate()
      return !validationProfile.value.$invalid
    }
    const profileUpdate = () => {
      if(validateProfile()) actionProfile()
    }
    const passwordUpdate = () => {
      if(refChangePassword.value.validate()) actionChangePassword()
    }
    const mailInCheck = () => {
      const path = route.fullPath
      const pathArr = path.split('/')

      // if (pathArr[1] === 'order' && me.value.is_location_mail_in === 0) {
      //   window.location.href = '/dashboard'
      // }
    }

    mailInCheck()

    watch(route, () => {
      mailInCheck()
      if (!isTrial) {
        isActiveSidebar.value = false
      }
    }, {
      deep: true
    })

    onMounted(async () => {
      screenWidth.value = window.innerWidth
      // 'https://app.refreshedtech.com'
      if(import.meta.env.VITE_ENVIRONMENT === 'production' && ['https://app.reusely.com'].includes(import.meta.env.VITE_API_URL)) {
        Userpilot.identify(`${me.value.customer_id}_${me.value.id}`, {
          name: `${me.value.first_name} ${me.value.last_name}`,
          email: me.value.email ? me.value.email : null,
          created_at: me.value.created_at ? me.value.created_at : null,
          company: {
            id: me.value.customer_id,
            name: me.value.company_name
          }
        })

        window.CustomerSupport.use('boot', {
          app_id: window.APP_ID,
          email: me.value.email ? me.value.email : null,
          created_at: me.value.created_at ? me.value.created_at : null,
          name: `${me.value.first_name} ${me.value.last_name}`,
          user_id: me.id,
          alignment: 'right',
          horizontal_padding: 20,
          vertical_padding: 90,
          custom_launcher_selector: '#intercom_launcher',
          ...intercomUserCustomAttributes.value
        })
        window.CustomerSupport.use('onShow', () => (intercomPanel.value = true))
        window.CustomerSupport.use('onHide', () => (intercomPanel.value = false))
      }
      window.addEventListener('resize', debounce(() => screenWidth.value = window.innerWidth, 300))
      window.onbeforeunload = (event) => {
        if(queue.size > 0) {
          event.preventDefault()
          return event.returnValue = "Changes you made may not be saved, continue anyway?"
        }
      }

      await store.dispatch('socket/init')

      await store.dispatch('socket/connect')
    })

    onUnmounted(() => {
      clearInterval(sessionChecker)
    })

    const handleMySettings = () => {
      router.push({
        name: 'accountSettings'
      })
    }
    const handleShowIntercom = () => {
      window.CustomerSupport.use('show')
    }

    onBeforeMount(() => {
      window.addEventListener('beforeunload', ev => {
        if(!isUnpaidBilling.value) return ''
        ev.preventDefault()
        ev.returnValue = ''
      })
    })

    const handleSwitchLocationSearch = async (e) => {
      switcherLocation.value.loadingLocation.all = true
      await store.dispatch('dashboard/changeLocation', {
        location_id: e.location.id
      }).then(async () => {
        await switcherLocation.value.locations()
        await getUser()
        switcherLocation.value.loadingLocation.all = false
        if (e.type === 'Local Buyback') router.push({name: 'buybackLocalDetail', params: {buybackId: e.box_id}})
        else if(e.type === 'Online Buyback') router.push({name: 'buybackOnlineDetail', params: {buybackId: e.box_id}})
        else router.push({name: 'invoiceDetail', params: {buybackId: e.box_id}})
      })
    }

    const handleGetUser = async () => await getUser()
    const handleToPlansAndBill = async () => {
      router.push({
        name: 'settingBilling'
      })
      // const response = await store.dispatch('billing/stripePortal')
      // if (response.url) {
      //   const targetPath = new URL(response.url)?.pathname || ''
      // }
    }

    watch(
      () => ({
        width: screenWidth.value,
        mobile: isActiveMobileSidebar.value
      }),
      (val) => {
        if (val.width >= 1024 && val.mobile) isActiveMobileSidebar.value = false
      }
    )

    return {
      refNavbar,
      switcherLocation,
      data,
      dataProfile,
      dataChangePassword,
      options,
      me,
      isTrialEnd,
      isTrial,
      intercomPanel,
      intercomLauncher,
      isLegacyPlan,
      formattedSubscriptionEndDate,
      formattedTrialEndDate,
      isUnpaidBilling,
      isCanceledSubscription,
      isMailinLocation,
      logout,
      routes,
      route,
      scanIn,
      aliasName,
      scanInData,
      selectedOrder,
      isSearchLoading,
      isActiveSidebar,
      isActiveMobileSidebar,
      isMobile,
      currentRouteHasStackedNav,
      currentParentRoute,
      currentParentRouteChildren,
      isNoPaddingContent,
      isHideNavMenu,
      isShowTrialEndWarning,
      changeSearch,
      modalProfile,
      validationProfile,
      loadingProfile,
      loadingLoginStatus,
      loadingChangePassword,
      profileUpdate,
      modalChangePassword,
      passwordUpdate,
      refChangePassword,
      screenWidth,
      validUserAgent,
      contentMinHeight,
      handleGetUser,
      handleToPlansAndBill,
      handleMySettings,
      handleShowIntercom,
      handleSwitchLocationSearch
    }
  }
})
</script>

